import React from "react";
import useStyles from "../styles";
import PhotoLibraryIcon from "@material-ui/icons/PhotoLibrary";

import {
  TableContainer,
  Table,
  Box,
  TableHead,
  TableRow,
  TableCell,
  TableBody,
  Grid,
  Typography,
} from "@material-ui/core";

export default function InformacionProducto({ producto, productoSeleccionado }) {
  const classes = useStyles();

  console.log(producto)
  console.log(productoSeleccionado)
  if (productoSeleccionado.length === 0) return null;

  return (
    <Grid container>
      <Grid item md={2} style={{display: "flex", justifyContent: "center"}}>
        {productoSeleccionado?.id_producto?.imagenes.length > 0 ? (
          <Box className={classes.containerImage}>
            <img
              alt="Imagen producto"
              src={productoSeleccionado?.id_producto?.imagenes[0].url_imagen}
              className={classes.imagen}
            />
          </Box>
        ) : (
          <Box p={4} display="flex" justifyContent="center" alignItems="center">
            <PhotoLibraryIcon style={{ fontSize: 40 }} />
          </Box>
        )}
      </Grid>
      <Grid item md={10}>
        <div className={classes.formInputFlex}>
          <Box width="100%">
            <Typography>
              <b>Nombre:</b>
            </Typography>
            <Box display="flex">
              <Typography>
                {
                  productoSeleccionado.id_producto.datos_generales.nombre_comercial
                }
              </Typography>
            </Box>
          </Box>
          <Box width="100%">
            <Typography>
              <b>Codigo Barras:</b>
            </Typography>
            <Box display="flex">
              <Typography>{productoSeleccionado.codigo_barras ? productoSeleccionado.codigo_barras : "N/A"}</Typography>
            </Box>
          </Box>
          <Box width="70%">
            <Typography>
              <b>Cantidad:</b>
            </Typography>
            <Box display="flex">
              <Typography>{productoSeleccionado?.cantidad}</Typography>
            </Box>
          </Box>
          <Box width="70%">
            <Typography>
              <b>Unidad:</b>
            </Typography>
            <Box display="flex">
              <Typography>{productoSeleccionado?.unidad}</Typography>
            </Box>
          </Box>
          <Box width="70%">
            <Typography>
              <b>Descuento:</b>
            </Typography>
            <Box display="flex">
              <Typography>
                {productoSeleccionado?.descuento
                  ? productoSeleccionado?.descuento.precio_neto
                  : 0}
              </Typography>
            </Box>
          </Box>
          <Box width="70%">
            <Typography>
              <b>% Descuento:</b>
            </Typography>
            <Box display="flex">
              <Typography>
                {productoSeleccionado?.descuento
                  ? productoSeleccionado?.descuento.precio_neto
                  : 0}
              </Typography>
            </Box>
          </Box>
        </div>
      </Grid>
      <Grid item lg={12}>
        <PreciosDeVentaCompras
          precios={productoSeleccionado?.id_producto?.precios?.precios_producto}
        />
      </Grid>
    </Grid>
  );
}

function PreciosDeVentaCompras({ precios }) {
  return (
    <div>
      <TableContainer>
        <Table aria-label="simple table" size="small">
          <TableHead>
            <TableRow>
              <TableCell style={{ minWidth: 150 }}>Precios de venta</TableCell>
              {[1, 2, 3, 4, 5, 6].map((numero, index) => (
                <TableCell style={{ minWidth: 100 }} key={index}>
                  {numero}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {["Precio de venta"].map((tipo, index) => (
              <TableRow key={index}>
                <TableCell style={{ border: 0 }}>
                  <b>{tipo}</b>
                </TableCell>
                {precios?.map((data, index) => (
                  <TableCell key={index} style={{ border: 0 }}>
                    {data?.precio_neto}
                  </TableCell>
                ))}
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
}
